.list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    column-gap: 1rem;
}

.card {
    display: flex;
    flex-direction: column;
}

.card-content {
    flex-grow: 1;
}

.recipe {
    margin: 1rem 2rem;
    display: flex;
    flex-direction: column;
}

.recipe img {
    max-width: 600px;
}

.video {
    align-self: center;
}

.video iframe {
    width: 600px;
    height: 400px;
}

@media (max-width: 767px) {
    .video {
        display: none;
    }
}